class TableData {
    constructor(type, rowCount, columnCount) {
        this.rowCount = rowCount;
        this.columnCount = columnCount;
        this.header = [];
        this.rows = [];
        
        switch (type) {
            case 'car_example':
                this.initCarExample();
                break;
            default:
                this.initDefaultTable();
                break;
        }  
    }

    initCarExample() {
        this.header = ['Model', 'Year', 'Color', 'Registration'];
        const cars = [
            ['Tesla', 2022, 'White', 'ABC-123'],
            ['Ferrari', 2020, 'Black', 'EFG-456'],
            ['Opel', 1999, 'Blue', 'VVX-111'],
            ['Citroen', 2002, 'White', 'HHJ-223'],
            ['Toyota', 1990, 'Grey', 'YYJ-990']
        ];

        let id = 0;
        
        cars.forEach(car => {
            let row = [];
            car.forEach(attribute => {
                row[row.length] = {id: id.toString(), value: attribute};
                id++;
            });
            this.rows[this.rows.length] = row;
        });
    }

    initDefaultTable() {
        for(let c=0; c<this.columnCount; c++) {
            this.header[c] ='Default Header ' + c;
        }

        let id = 0;
        for(let r=0;r<this.rowCount; r++) {
            let row = [];
            for(let c=0; c<this.columnCount; c++) {
                row[c] = {id: id.toString(), value: 'Default Cell'};
                id++;
            }
            
            this.rows[r] = row; 
        }

    }

    getHeaderData() {
        return this.header;
    }

    getRowData() {
        return this.rows;
    }

    setCell(row, column, value) {
        if (!this.isValidRow(row) || !this.isValidColumn(column))
            return;
        this.rows[row][column].value = value;
    }

    setRow(row, value) {
        if (!this.isValidRow(row))
            return;
        for (let i=0; i < this.rows[row].length; i++) {
            this.rows[row][i].value = value;
        }       
    }

    setColumn(column, value) {
        if (!this.isValidColumn(column))
            return;
        for (let r=0; r < this.rows.length; r++) {
            this.rows[r][column].value = value;
        }
    }

    isValidRow(rowNumber) {
        if (rowNumber < 0 || rowNumber >= this.rowCount)
            return false;
        return true;
    }

    isValidColumn(columnNumber) {
        if (columnNumber < 0 || columnNumber >= this.columnCount)
            return false;
        return true;
    }
}

export default TableData;