import {useState, useEffect} from 'react';
import TableData from "./TableData";
import {ClassName} from "../Other/HtmlHandlers";
import "./TableExercise.css";

function TableHeader({headerData, id, sorting, HeaderClickedCallBack}) {
    return (
        <thead>
            <tr>
                {headerData.map((hItem, index) => 
                    <th key={id + '-h' + index} id={id + '-h' + index} onClick={() => HeaderClickedCallBack(index)}>
                        {hItem}
                        {index === sorting.columnIndex && <TableSortingIcon sortAscending={sorting.ascending} />}
                    </th>
                )}
            </tr>
        </thead>
    );
}

function TableSortingIcon({sortAscending}) {
    const rectangleStyle = {
        fill: 'var(--mainColor1)',
        stroke: 'var(--mainColor2)',
        strokeWidth: '5',
        opacity: '1'
    };

    const triangleStyle = {
        fill: 'var(--mainColor2)',
        opacity: '1'
    };
    
    const triangleUP = () => {
        return <path d="M20 80 L50 20 L80 80 Z" style={triangleStyle} />;
    }

    const triangleDOWN = () => {
        return <path d="M20 20 L50 80 L80 20 Z" style={triangleStyle} />;
    }

    return (
        <svg viewBox="0 0 100 100">
    	    <rect x="3" y="3" rx="20" ry="20" width="94" height="94" style={rectangleStyle} />
  		    {sortAscending ? triangleUP() : triangleDOWN()} 
	    </svg>
    );
}

function TableRows({rowData, id, selectedCell, CellSelectedCallBack}) {
    return (
        <tbody>
            {rowData.map((rItem, index) => <tr key={id + '-r' + index} id={id + '-r' + index}><TableColumns columnData={rItem} id={id} selectedCell={selectedCell} CellSelectedCallBack={CellSelectedCallBack}/></tr>)}
        </tbody>
    );
}

function TableColumns({columnData, id, selectedCell, CellSelectedCallBack}) {
    const tableCell = (cid, value) => {
        return <td key={id + '-c' + cid} id={id + '-c' + cid} className={selectedCell === cid ? 'selected' : ''} onClick={() => CellSelectedCallBack(cid)} >{value}</td>
    }

    return columnData.map((cItem) => tableCell(cItem.id, cItem.value));
}

function Table({tableData, id}) {
    const [headerData, setHeaderData] = useState(tableData.getHeaderData());
    const [rowData, setRowData] = useState(tableData.getRowData());
    const [selectedCell, setSelectedCell] = useState('0');
    const [sorting, setSorting] = useState({columnIndex: 0, ascending: true});

    useEffect(() => {
        SortTable(sorting);
    }, [sorting]);
    
    const SortTable = (sorting) => {
        //console.log('Sorting table by...: ' + sorting.columnIndex + ' ' + sorting.order);
        setRowData(prevRowData => {
            return [...prevRowData].sort((a, b) => {
                // Sort upper and lower case strings properly.
                let aValue = a[sorting.columnIndex].value.toString().toUpperCase(),
                    bValue = b[sorting.columnIndex].value.toString().toUpperCase();
                
                // Sort numbers properly.
                if (Number(aValue) && Number(bValue)) {
                    aValue = Number(aValue);
                    bValue = Number(bValue);
                }
                   
                if (aValue === bValue)
                    return 0;
                if (aValue > bValue)
                    if (sorting.ascending)
                        return 1;
                    else
                        return -1;
                else
                    if (sorting.ascending)
                        return -1;
                    else
                        return 1;
            });
        });
    }

    const CellSelected = (id) => {
        //console.log(id);
        setSelectedCell(id);
    }

    const HeaderClicked = (index) => {
        //console.log(index);
        setSorting(prevSorting => {
            return {
                ...prevSorting,
                columnIndex: index,
                ascending: !prevSorting.ascending
            };
        });
    }



    const GetSelectedCellValue = () => {
        if (selectedCell === '')
            return '';
        let cellValue = '';
        let cellIndexes = GetSelectedCellIndexes(rowData);
        cellValue = rowData[cellIndexes.rowIndex][cellIndexes.columnIndex].value;
        //console.log(cellValue);
        return cellValue;
    }

    const GetSelectedCellIndexes = (rowData) => {
        let row;
        for (let r = 0; r < rowData.length; r++) {
            row = rowData[r];
            for (let c = 0; c < row.length; c++) {
                if (row[c].id === selectedCell)
                    return {rowIndex: r, columnIndex: c};
            }
        }

        return {rowIndex: 0, columnIndex: 0};
    }

    const UpdateCell = (value) => {
        let selectedCellIndexes = GetSelectedCellIndexes(rowData);
        let rowIndex = selectedCellIndexes.rowIndex;
        let columnIndex = selectedCellIndexes.columnIndex;
        
        setRowData(prevRowData => {
            return prevRowData.map((rItem, index) => {
                if (index !== rowIndex) return rItem;
                return rItem.map((cItem, index) => {
                    if (index !== columnIndex) return cItem;
                    return {
                      ...cItem,
                      value: value
                    };
                }) 
            })
        });
    }

    return (
        <>
        <table id={id}>
            <TableHeader headerData={headerData} id={id} sorting={sorting} HeaderClickedCallBack={HeaderClicked} />
            <TableRows rowData={rowData} id={id} selectedCell={selectedCell} CellSelectedCallBack={CellSelected} />
        </table>
        <TableEdit value={GetSelectedCellValue} UpdateCellCallBack={UpdateCell} />
        </>
    );
}

function TableEdit({value, UpdateCellCallBack}) {
    const [cellValue, setCellValue] = useState(value);

    useEffect(() => {
        setCellValue(value);
    }, [value]);
    
    const HandleCellEdit = (event) => {
        event.preventDefault();
        //console.log('Value Submitted: ' + cellValue);
        UpdateCellCallBack(cellValue);
    }

    return(
        <form onSubmit={HandleCellEdit}>
            <label>Edit Selected Cell
                <input
                    className='cell-edit-field' 
                    type='text'
                    value={cellValue}
                    onChange={(event) => setCellValue(event.target.value)}
                />
            </label> 
            <input value='Save' type='submit' className='save-input' />
        </form>
    );
}

function TableExercise(props) {
    const carData = new TableData('car_example');
    const tData = new TableData(null, props.rowCount, props.columnCount);
    tData.setCell(2, 2, 'Altered Cell!');
    tData.setRow(0, 'Altered Rows!');
    tData.setColumn(1, 'Altered Columns!');
    tData.setCell(0, 0, '1');
    tData.setCell(1, 0, '20');
    tData.setCell(2, 0, '56');
    tData.setCell(3, 0, '78.45');
    tData.setCell(4, 0, '78.10');
    
    return (
        <div id='table-exercise'>
            <Table tableData={carData} id='tb1' />
            <Table tableData={tData} id='tb2' />
        </div>
    )
}

export default TableExercise;